import React, { useState } from "react";
import { Navigate } from "react-router-dom";
import { withAuthRedirect } from "../../Hoc/withAuthRedirect";
import { Button } from "antd";
import { getRandomBeer } from '../../Redux/settingPageReducer'
import { compose } from "redux";
import { connect } from "react-redux";
import style from './Settings.module.css'
import anonymBeer from '..//../Images/anonymBeer.png'
import { v1 } from 'uuid'
import './Setting.css';
import Preloader from "../Common/Preloader/preloader";


const BeerComp = (props) => {

    return <div>

        <div className={style.buttonWrapper}><button className={style.button} onClick={() => props.getRandomBeer()} >  !</button></div>
        {!props.beer
            ? <div>
                {props.beer.map(u =>
                    <div className={style.component} key={u.id}>
                        <div><img src={u.image_url || anonymBeer} /></div>
                        <div>
                            <ul> - {u.name}</ul>
                            <ul> - {u.description}</ul>
                            <ul>   - {u.first_brewed}</ul>
                            <ul> - {u.boil_volume.value} {u.boil_volume.unit} </ul>
                            <ul> - {u.brewers_tips}</ul>
                            <ul>   : <ul>{u.food_pairing.map(e => <li key={v1()} >{e}</li>)} </ul></ul>
                            <ul>:
                                <ul> {u.ingredients.hops.map(i =>
                                    <li key={v1()}>{i.name},  - {i.attribute},   - {i.add}, - {i.amount.value} </li>)}
                                </ul>
                                <ul> - {u.ingredients.malt.map(m => <li key={v1()}>{m.name}, - - {m.amount.value} </li>)}
                                </ul>
                            </ul>
                            <ul> :
                                <ul>
                                     - {u.method.fermentation.temp.value} 
                                </ul>
                                <ul>
                                     - {u.method.mash_temp.map(t => <span key={v1()}> - {t.duration || '()'} ,  - {t.temp.value} </span>)}
                                </ul>
                            </ul>
                            <ul> - {u.method.twist || '()'}</ul>
                            <ul>    - {u.brewers_tips}</ul>
                        </div>
                    </div>)}
            </div>
            : <div> {props.beer}  , ,    API  </div>}

    </div>
}
